import React from 'react'
import { FaCheckCircle, FaTimesCircle } from 'react-icons/fa';
import { ImCheckboxChecked, ImCheckboxUnchecked } from 'react-icons/im';
import { MdBookmark } from 'react-icons/md';
import { RiShieldCheckFill } from 'react-icons/ri';
import { LanguageInterface, QuestionAnswerInterface, QuestionItemInterface, questionTypeElementsType } from '../../../../../../interfaces/question';
import { validateCalculatedFormula } from '../../../../../../utils/question_bank';
import { SelectedKeysEdit } from '../EditQuestion';

interface MultipleChoiceProps {
  selectedKey: SelectedKeysEdit | null;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  editSingleMultipleStatus: (answer_id: string, status: boolean) => void;
  removeAnswerSingleMultiple: (question: QuestionItemInterface[], removedAnswer: QuestionAnswerInterface) => void;
  selectedQuestionItem: QuestionItemInterface;
  selectedQuestionType: questionTypeElementsType | null;
}

const MultipleChoice = (props: MultipleChoiceProps) => {
  const checkCalculateType = props.selectedQuestionType === questionTypeElementsType.CalculatedMultipleChoice ? true : false;
  return (
    <div>
      {props.selectedQuestionItem.answer.map((item, i) => (
      <div key={i + 1} className={`flex flex-row items-start justify-between mb-2 gap-3 ${props.selectedKey !== SelectedKeysEdit.QuestionAnswer ? "border-b border-gray-300" : ""}`}>
        {props.selectedKey === SelectedKeysEdit.QuestionAnswer ?
        <div className="w-full">
          <input
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
              if (check === undefined) {
                check = props.wholeQuestion[0];
              }
              props.updateAnswerSingleMultiple(
                check, item.value, {
                  value: e.target.value,
                  status: item.status,
                  id: item.id,
                }
              )
            }}
            value={`${item.value}`}
            type="text" 
            className={`${checkCalculateType === true ? validateCalculatedFormula(item.value).status === true ? "input-gray-lg border-gray-500 focus:border-blue-600" : "input-gray-lg-danger border-yellow-600 focus:border-yellow-700" : "input-gray-lg"} w-full border-2 border-gray-500`} /> 
            <span className={`${validateCalculatedFormula(item.value).status === true ? "text-green-600" : "text-yellow-700"} text-xs font-bold`}>{validateCalculatedFormula(item.value).msg}</span>
          </div>:
          <div className="w-full flex flex-row items-center gap-3 py-2">
            {item.status === true ?
            <RiShieldCheckFill className="text-2xl text-blue-600" /> :
            <MdBookmark className="text-2xl text-gray-500" />}
            {item.value}
          </div>}
          {props.selectedKey === SelectedKeysEdit.QuestionAnswer ?
          <div 
            onClick={() => {
              let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
              if (check === undefined) {
                check = props.wholeQuestion[0];
              }
              props.editSingleMultipleStatus(
                item.id, !item.status
              )
            }}
            className={`flex flex-row items-center gap-2 cursor-pointer ${item.status === true ? "text-blue-600" : "text-gray-600"} hover:text-blue-600 w-40 mt-2`}>
              {item.status === false ?
              <ImCheckboxUnchecked className="text-2xl cursor-pointer" /> :
              <ImCheckboxChecked className="text-2xl cursor-pointer" />}
              <span className="font-bold ">{item.status === true ? "Correct" : "Incorrect"}</span>
            </div> :
            <div className="w-28 flex justify-end text-center">
              <div className={`font-bold px-3 py-2 rounded-full w-28 ${item.status === true ? "text-green-600 bg-white" : "text-gray-600 bg-white"}`}>
                {item.status === true ? 
                  <div className="flex flex-row gap-2 items-center">
                    <FaCheckCircle className="text-xl" /> <span>Correct</span>
                  </div> : 
                  <div className="flex flex-row gap-2 items-center">
                    <FaTimesCircle className="text-xl" /> <span>Incorrect</span>
                  </div>
                }
              </div>
            </div>}
          <div 
          onClick={() => {
            props.removeAnswerSingleMultiple(props.wholeQuestion, item)
          }}
          className="btn bg-red-50 text-red-600 hover:bg-red-200 hover:text-red-800 cursor-pointer">Remove</div>
      </div>))}
    </div>
  )
}

export default MultipleChoice
