import React, { useState } from 'react'
import { RiShieldCheckFill } from 'react-icons/ri';
import { LanguageInterface, NumericalQuestionAnswerInterface, QuestionAnswerInterface, QuestionItemInterface } from '../../../../../../interfaces/question';
import { toNumerical } from '../../../../../DoExam/QuestionDetails/ConvertingAnswers';
import { SelectedKeysEdit } from '../EditQuestion';
import { MdError } from 'react-icons/md';

interface NumericalProps {
  selectedKey: SelectedKeysEdit | null;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  updateAnswerSimpleCalculate: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  editSingleMultipleStatus: (answer_id: string, status: boolean) => void;
  removeAnswerSingleMultiple: (question: QuestionItemInterface[], removedAnswer: QuestionAnswerInterface) => void;
  selectedQuestionItem: QuestionItemInterface;
  updateAnswerNumerical: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  updateAnswerNumericalValue: (question: QuestionItemInterface, answer_id: string, editedAnswer: NumericalQuestionAnswerInterface) => void;
}

const Numerical = (props: NumericalProps) => {
  const [editQuestionAnswer, setEditQuestionAnswer] = useState<string>(props.selectedQuestionItem.answer[0].value[0].toString());
  const [editQuestionAnswerStatus, setEditQuestionAnswerStatus] = useState<string>(props.selectedQuestionItem.answer[0].value[1].toString());
  // const updateQuestionAnswer = (value: string, id: string, status: number[], ) => {
  //   let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
  //   if (check === undefined) {
  //     check = props.wholeQuestion[0];
  //   }
  //   props.updateAnswerNumericalValue(
  //     check, id, {
  //       value: [parseInt(value)],
  //       status: status,
  //       id: id,
  //     }
  //   )
  // }
  return (
    <div className={`${props.selectedKey === SelectedKeysEdit.QuestionAnswer ? "" : "bg-blue-50"} -mt-3 px-2 pt-2 pb-1 rounded`}>
      {toNumerical(props.selectedQuestionItem.answer).map((item, i) => (
      <div key={i + 1} className={`flex flex-row items-center justify-between mb-2 gap-3 ${props.selectedKey !== SelectedKeysEdit.QuestionAnswer ? "text-blue-600 font-bold" : ""}`}>
        {props.selectedKey === SelectedKeysEdit.QuestionAnswer ?
        <>
          <div className="flex flex-col w-full">
            <div>Correct answer</div>
            <input
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
              if (check === undefined) {
                check = props.wholeQuestion[0];
              }
              setEditQuestionAnswer(e.target.value);
              if (editQuestionAnswer !== "" && !isNaN(parseFloat(editQuestionAnswer))) {
                props.updateAnswerNumericalValue(
                  check, item.id, {
                    value: [parseFloat(e.target.value)],
                    status: item.status,
                    id: item.id,
                  }
                )
              }
            }}
            value={`${editQuestionAnswer}`}
            type="text" 
            className="input-gray-lg w-full border-2 border-gray-500" />
          </div>
        </> :
        <div className="w-full flex flex-row items-center justify-center gap-3 py-2 border border-blue-300 bg-white rounded">
          <RiShieldCheckFill className="text-2xl text-blue-600" />
          <span>Answer: </span>
          <span>{item.value[0]}</span>
        </div>}
        {props.selectedKey === SelectedKeysEdit.QuestionAnswer ?
        <div className={`flex flex-row items-center gap-2 cursor-pointer  w-full`}>
          <div className="flex flex-col w-full">
            <div>Error on the answer</div>
            <input
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
              if (check === undefined) {
                check = props.wholeQuestion[0];
              }
              setEditQuestionAnswerStatus(e.target.value);
              if (editQuestionAnswerStatus !== "" && !isNaN(parseFloat(editQuestionAnswerStatus))) {
                props.updateAnswerNumericalValue(
                  check, item.id, {
                    value: item.value,
                    status: [parseFloat(e.target.value)],
                    id: item.id,
                  }
                )
              }
            }}
            value={`${editQuestionAnswerStatus}`}
            type="text" 
            className="input-gray-lg w-full border-2 border-gray-500" />
          </div>
        </div> :
        <div className="w-full flex flex-row items-center justify-center gap-3 py-2 bg-white rounded">
          <MdError className="text-2xl text-gray-500" />
          <span className="text-gray-500">Error: </span>
          <span className="text-gray-500">{item.value[1]}</span>
        </div>}
      </div>))}
    </div>
  )
}

export default Numerical
